  margin-bottom: 30px;
  width: 100%;
  max-width: 100%;
  vertical-align: top; }
  .docs-example > code {
    margin-bottom: 10px; }
  .docs-example[data-modifier="default"]:last-child > code {
    display: none; }

.docs-modifiers code,
.docs-example > code {
  display: inline-block;
  line-height: 1.4;
  white-space: nowrap; }
  .docs-modifiers code:empty,
  .docs-example > code:empty {
    display: none; }

[data-modifier="default"] > code {
