/*
CSS `border` property issues:
- An element can only have one border.
- Borders can't stack with shadows.
- Borders modify the size of the element they're applied to.
- Border positioning requires the extra `box-sizing` property.

`box-shadow` doesn't have these issues, we're using it instead of `border`.
*/
/*
Overlay shadows are used for default buttons
floating on top of other elements. This way, the
shadows blend with the colors beneath it.
Switches and slider handles both use these variables.
*/
/*
Flex utils
*/
.docs-flex-row {
  display: flex;
  flex-direction: row; }

.docs-flex-column {
  display: flex;
  flex-direction: column; }

/*
Page layout elements
*/
.docs-root {
  background-color: #f5f8fa;
  min-height: 100vh; }
  .docs-root.pt-dark {
    background-color: #293742; }

.docs-app {
  margin: auto;
  width: 1000px;
  padding-top: 50px; }

.docs-navbar {
  position: fixed;
  top: 0;
  width: inherit; }

.docs-content {
  margin-left: 220px;
  padding: 0 10px 30px 30px; }

.docs-nav {
  position: fixed;
  top: 50px;
  bottom: 0;
  width: 220px;
  overflow: auto;
  padding: 30px 10px; }

/*
Lefthand navigation menu
*/
.docs-nav-menu .docs-nav-menu {
  display: none; }

.docs-nav-expanded > .docs-nav-menu {
  display: block; }

.docs-nav-expand-all .docs-nav-menu {
  display: block !important; }

.docs-empty-state {
  display: none; }

.docs-nav-menu:empty + .docs-empty-state {
  display: block; }

.docs-menu-item .pt-menu-item {
  padding-right: 10px;
  padding-left: 10px;
  white-space: initial; }

.docs-menu-item.depth-2 > .pt-menu-item {
  padding-left: 30px; }

.docs-menu-item.depth-3 > .pt-menu-item {
  padding-left: 50px; }

.docs-menu-item.depth-4 > .pt-menu-item {
  padding-left: 70px; }

.docs-menu-item.depth-5 > .pt-menu-item {
  padding-left: 90px; }

.docs-menu-item.depth-6 > .pt-menu-item {
  padding-left: 110px; }

